.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.box-container{
  padding-bottom: 20rem;

}

.box {
  margin-left: 5rem;
  width: 5rem;
  height: 5rem;
  background-color: black;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.skillContainer {
  margin-top: 50px;
  padding: 0;
  display: flex;
  width: 100%;
  justify-content: center;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  
}

.skillContainer .hexagonArea{
  display: flex;
  max-width: 100%;
}

.skillContainer .hexagonArea.second{
  transform: translateY(-2.063rem);

}

.skillContainer .hexagonArea .hexagon{
  position: relative;
  width: 9.375rem;
  height: 10.625rem;
  font-size: calc(60% + 0.8vmin);
  margin: 0 5px;
  clip-path: polygon(0 25%, 50% 0, 100% 25%, 100% 75%, 50% 100%, 0 75%);
  background: black;
  color: white;
  display: flex;
  justify-content: center;
  text-align: center;
  align-items: center;
  flex-direction: column;
}